/*desktop styles*/
/*sticky footer*/
html,
body {
  height: 100%;
  background: #efefef;
  color: #333333;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
html .sticky-footer-wraper,
body .sticky-footer-wraper {
  min-height: 100%;
  margin-bottom: -60px;
  /* equal to footer height */
}
html .sticky-footer-wraper:after,
body .sticky-footer-wraper:after {
  content: "";
  display: block;
}
html .sticky-footer,
body .sticky-footer,
html .sticky-footer-wraper:after,
body .sticky-footer-wraper:after {
  height: 60px;
}
html .sticky-footer,
body .sticky-footer {
  /*background: orange;*/
}
/*sticky footer end*/
.phpinfo-wrapper .center {
  color: #000000;
  text-align: center;
  overflow-x: auto;
}
.phpinfo-wrapper .center td,
.phpinfo-wrapper .center th,
.phpinfo-wrapper .center h1,
.phpinfo-wrapper .center h2 {
  font-family: sans-serif;
}
.phpinfo-wrapper .center pre {
  margin: 0px;
  font-family: monospace;
}
.phpinfo-wrapper .center a:link {
  color: #000099;
  text-decoration: none;
}
.phpinfo-wrapper .center a:hover {
  text-decoration: underline;
}
.phpinfo-wrapper .center h1 {
  font-size: 150%;
}
.phpinfo-wrapper .center h2 {
  font-size: 125%;
}
.phpinfo-wrapper .center .p {
  text-align: left;
}
.phpinfo-wrapper .center .e {
  background-color: #ccccff;
  font-weight: bold;
  color: #000000;
}
.phpinfo-wrapper .center .h {
  background-color: #9999cc;
  font-weight: bold;
  color: #000000;
}
.phpinfo-wrapper .center .v {
  background-color: #cccccc;
  color: #000000;
}
.phpinfo-wrapper .center .vr {
  background-color: #cccccc;
  text-align: right;
  color: #000000;
}
.phpinfo-wrapper .center img {
  float: right;
  border: 0px;
}
.phpinfo-wrapper .center hr {
  width: 600px;
  background-color: #cccccc;
  border: 0px;
  height: 1px;
  color: #000000;
}
.phpinfo-wrapper .center table {
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  max-width: 600px;
  table-layout: fixed;
  border-collapse: collapse;
}
.phpinfo-wrapper .center table th {
  text-align: center !important;
}
.phpinfo-wrapper .center table td {
  word-wrap: break-word;
}
.phpinfo-wrapper .center table td,
.phpinfo-wrapper .center table th {
  border: 1px solid #000000;
  font-size: 75%;
  vertical-align: baseline;
}
.form-control.has-error {
  color: #a94442;
  border-color: #a94442;
}
.table th.views {
  min-width: 150px;
}
.table th.date-utc {
  min-width: 160px;
}
.table th.action {
  min-width: 140px;
}
.table th.status {
  min-width: 115px;
}
.table .action-buttons a {
  margin-bottom: 15px;
}
.properties-list.deletable li:after {
  display: none;
  content: '\D7';
  position: absolute;
  top: 0px;
  right: 5px;
  color: #a94442;
  font-size: 150%;
  font-weight: bold;
}
.properties-list.deletable li:hover:after {
  display: block;
}
.properties-list li {
  padding-top: 10px;
  cursor: pointer;
  position: relative;
}
.properties-list li:hover {
  background-color: #337ab7;
  color: #efefef;
}
#item-embed {
  display: none;
}
.gallery-item {
  margin-bottom: 15px;
}
.gallery-item > button {
  position: absolute;
  top: 10px;
  right: 25px;
}
.mce-panel {
  border-radius: 5px;
  margin: 0 15px !important;
}
.table-hover tbody tr.bg-success:hover {
  background-color: #e5f3df;
}
.table-hover tbody tr.bg-danger:hover {
  background-color: #f5e5e5;
}
.loadanimation {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.75);
  z-index: 11000;
}
.loadanimation .spinner {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 50px;
  height: 50px;
}
.loadanimation .spinner .fa {
  font-size: 50px;
}
